  .container {
    display : flex;
    flex-direction: column;
    margin: auto;
    width: 600px;
    margin-top: 200px;
    background: #fff;
    padding-bottom: 30px;
  } 

  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin: 30px;
  }

  .text {
    font-size: 48px;
    font-weight: 700;
    color: #3c009d;
  }
  .underline{
    width: 61px;
    height: 6px;
    background-color: #3c009d;
    border-radius: 9px;
  }
  .inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #f0f0f0;
    border-radius: 6px;
  }

  .input img{
    margin: 0px 30px;
  }
  .input input{
    height: 20px;
    width: 250px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 20px;
  }
  .forgot-password{
    padding-left:62px;
    margin-top: 27px;
    font-size: 18px;
    color: #797979;
  }

  .forgot-password:hover{
    cursor: pointer;
    color: #4c00b4;
  }

  .submit-container{
    display: flex;
    gap: 30px;
    margin: 60px auto;
  }
  .submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 60px;
    background: #4c00b4;
    color: #fff ;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
  }
  .gray{
    background: #EAEAEA;
    color: #676767;
  }